@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:ital,wght@0,300;0,400;1,700&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    /* font-family: 'Playfair Display', serif;
    font-family: 'Poppins', sans-serif; */
}


/* Navbar Css */

body {
    background-color: #94734B;
    color: white;
}

.navtitle31 {
    font-size: 5rem;
    text-align: center;
    height: 10vh;
    line-height: 10vh;
    color: #fcedd8;
    background: #94734B;
    font-family: 'Niconne', cursive;
    font-weight: 700;
    text-shadow: 5px 5px 0px #eb452b, 10px 10px 0px #efa032, 15px 15px 0px #46b59b, 20px 20px 0px #017e7f, 25px 25px 0px #052939
}

.hrline {
    width: 100px;
    height: 200px;
}

.img-home {
    width: 100%;
    background-color: #94734B;
    display: flex;
    justify-content: center;
}

.img-home img {
    height: 80%;
}

.card {
    color: white;
    border: 2px solid white;
    background-color: #94734B;
    border-radius: 6px 6px 10px 10px;
}

.filter-side {
    border-right: 2px solid white;
}

.navbar {
    font-family: 'Poppins', sans-serif;
    padding: "5px";
    font-size: 17px;
    line-height: 20px;
    text-transform: uppercase;
    box-shadow: 0 8px 6px -6px gray;
    --webkit-box-shadow: 0 20px 20px 20px gray;
    border-bottom: 0.8px solid gray !important;
}

.nav-link {
    font-weight: 300 !important;
}

.active {
    border-bottom: 1px solid gray;
}

.navbar-brand {
    font-weight: 700;
    font-family: "roboto";
    letter-spacing: 3px;
}


/* Footer */

.footer {
    color: white;
    background: linear-gradient(to right, rgb(26, 22, 22), rgb(246, 230, 230));
}

.footer a {
    color: black;
    text-decoration: none;
    padding: 10px;
}

.footer a:hover {
    color: rgb(255, 252, 252);
}


/* Page Not Found */

.pgnf_cont {
    display: flex;
    flex-direction: column;
    min-height: 65vh;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.pg_title {
    font-size: 5rem;
    margin-bottom: 5px;
}

.pgnf_btn {
    margin-top: 5px;
    color: black;
    background-color: rgb(255, 255, 255);
    padding: 8px;
    border: 2px solid gray;
}

.pgnf_btn:hover {
    background-color: black;
    color: white;
    border: 2px solid gray;
}


/* Contact Us page */

.contactus {
    padding: 10px;
}


/* link editing */

.product-link {
    text-decoration: none !important;
    color: black !important;
}

.productdetails {
    margin: 0;
    padding: 0;
}

.nav-cart {
    margin-top: 5px;
    font-size: 5px;
}

.home_cards {
    margin: 20px;
    justify-content: space-between;
}

.loadbtn {
    margin: -30px 0 20px 20px;
    padding-left: 20px;
}

.sticky-circle {
    position: sticky;
    top: 50%;
    /* Adjust the vertical position */
    right: 100%;
    /* Adjust the horizontal position */
    transform: translate(-50%, -50%);
    width: 200px;
    /* Adjust the width of the circle */
    height: 200px;
    /* Adjust the height of the circle */
    background-color: #80623F;
    /* Adjust the circle's background color */
    border-radius: 50%;
    /* Make it a circle */
    z-index: -9;
    /* Place it behind the content */
    opacity: 0.7;
    /* Adjust the opacity if needed */
}

.tablestyle {
    color: white;
    text-decoration: none;
    list-style: none;
}


/* user menu */

.menu_items {
    background-color: black;
}